export default {
	
	data() {
		return {
			currentNumber: 0, // 用来判断active样式类是否显示
			currentNumberBtn: 0,
			statusChange: '',
			curPage: 1,
			status: 0,
			nameList: [{
				title: '充值',
				time: '2019-09-10 14:30:15',
				money: '+1000BTC',
				status: '已完成'
			}],
			id: '',
			acid: '',
			list: [{
					title: "币种详情"
				},
				{
					title: "充提记录"
				}
			],
			twoBtn: [{
				title: '转出'
			}, {
				title: '转入'
			}],
			nameList2: [{
					leftText: '发行时间:',
					rightText: '2008-11-10'
				},
				{
					leftText: '发行总量:',
					rightText: '21,000,000'
				},
				{
					leftText: '流通总量：',
					rightText: '17,816,737'
				},
				{
					leftText: '众筹价格：',
					rightText: '17,816,737'
				},
				{
					leftText: '白皮书：',
					rightText: '17,816,737'
				},
				{
					leftText: '官网：',
					rightText: '17,816,737'
				},
				{
					leftText: '区块链查询：',
					rightText: '17,816,737'
				},
				{
					leftText: '手机钱包下载地址：',
					rightText: '17,816,737'
				},
				{
					leftText: 'PC钱包下载地址：',
					rightText: '17,816,737'
				}

			],
			money: '',
			forzen: '',
			price: '',
			coinId: '',
			showDetail: true
		};
	},
	onLoad(options) {
		this.coinId = options.coinId
		this.money = options.money
		this.forzen = options.foezen
		this.price = options.price
		this.getCoreDetail()
	},
	onPullDownRefresh() {
		this.getCoreDetail()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		currentInfo(index) {
			this.currentNumber = index;
			if(this.currentNumber == 1){
				this.showDetail=false
			}
			//单个币种的充提记录
			uni.request({
				url: this.baseUrl + "/recharge-withdraw",
				data:{
					page:1,
					count:100000,
					Id:this.coinId
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if (res.data.status == 1) {
						// this.nameList = res.data.data.data
						console.log(this.nameList)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		currentBtnIndex(index) {
			this.currentNumberBtn = index
		},
		//根据币种Id获取币种
		getCoreDetail() {
			uni.request({
				url: this.baseUrl + "/single-coin",
				data: {
					Id: this.coinId
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base._indexOf(res.data.status)) {
						this.$base._isLogin()
					} else if (res.data.status == 1) {


					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		showState(state) {
			if (state == 1) {
				return "待理财";
			} else if (state == 2) {
				return "理财中"
			} else if (state == 3) {
				return "已撤资"
			} else if (state == 4) {
				return "已完结"
			}
		},
		jumpToManage(index) {
			this.id = this.nameList[index].Id
			this.acid = this.nameList[index].ActivityId
			console.log(this.id) //我的理财id
			console.log(this.acid) //理财活动id
			uni.navigateTo({
				url: "./manage-in?id=" + this.id + "&acid=" + this.acid //需要传一个investmentId过去,还需传一个acid过去获取天数
			})
		}
	}
}
